import {profileAPI} from "../api/api";
import {stopSubmit} from "redux-form";
import {PhotosType, ProfileType} from "../types/types";

const ADD_POST = 'socialNetwork/profile/ADD_POST';
const DELETE_POST = 'socialNetwork/profile/DELETE_POST';
const SET_USER_PROFILE = 'socialNetwork/profile/SET_USER_PROFILE';
const LOOKING_FOR_JOB = 'socialNetwork/profile/LOOKING_FOR_JOB';
const SET_STATUS = 'socialNetwork/profile/SET_STATUS';
const SAVE_PHOTO_SUCCES = 'socialNetwork/profile/SAVE_PHOTO_SUCCES';

type PostType = {
    userId: number,
    postText: string,
    likeCounter: number
}

let initialState = {
    posts: [
        {userId: 1, postText: 'How r u?', likeCounter: 1},
        {userId: 2, postText: 'Hi!', likeCounter: 0},
        {userId: 3, postText: 'What?', likeCounter: 5},
    ] as Array<PostType>,
    profile: null as ProfileType | null,
    //lookingForAJob: false,
    status: '',
};

export type initialStateType = typeof initialState;

const profileReducer = (state = initialState, action:any):initialStateType => {
    switch (action.type) {
        case ADD_POST:
            return {
                ...state,
                posts: [...state.posts, {userId: 6, postText: action.newPost, likeCounter: 0}]
            };
        case DELETE_POST:
            return {
                ...state,
                posts: state.posts.filter(post => post.userId != action.id)
            };
        case SET_USER_PROFILE:
            return {
                ...state,
                profile: action.profile
            };
        case SET_STATUS:
            return {
                ...state,
                status: action.status
            };
        case SAVE_PHOTO_SUCCES:
            return {
                ...state,
                //Need refactor type
                profile: {...state.profile, photos: action.file} as ProfileType
            };
        default:
            return state;
    }
};

type AddPostType = {
    type: typeof ADD_POST
    newPost: string
}
export const addPost = (newPost: string):AddPostType => ({type: ADD_POST, newPost});
type DeletePostType = {
    type: typeof DELETE_POST
    id: number
}
export const deletePost = (id: number): DeletePostType => ({type: DELETE_POST, id});
type SetUserProfileType = {
    type: typeof SET_USER_PROFILE
    profile: ProfileType
}
export const setUserProfile = (profile: ProfileType): SetUserProfileType => ({type: SET_USER_PROFILE, profile});
type SetStatusType = {
    type: typeof SET_STATUS
    status: string
}
export const setStatus = (status: string): SetStatusType => ({type: SET_STATUS, status});
type SavePhotoSuccessType = {
    type: typeof SAVE_PHOTO_SUCCES
    photos: PhotosType
}
export const savePhotoSuccess = (photos: PhotosType): SavePhotoSuccessType => ({type: SAVE_PHOTO_SUCCES, photos});


export const getUserProfile = (id: number) => async (dispatch: any) => {
    const data = await profileAPI.getUserProfile(id);
    dispatch(setUserProfile(data));
};

export const getUserStatus = (userId: number) => async (dispatch: any) => {
    const data = await profileAPI.getUserStatus(userId);
    dispatch(setStatus(data));
};

export const updateUserStatus = (status: string) => async (dispatch: any) => {
    try {
        const response = await profileAPI.updateUserStatus(status);
        if (response.data.resultCode === 0) {
            dispatch(setStatus(status))
        }
    }
    catch (error) {
        alert('error')
        console.error(error)
        console.log(error)
    }
};

export const savePhoto = (photos: PhotosType) => async (dispatch: any) => {
    const response = await profileAPI.savePhoto(photos);
    if (response.data.resultCode === 0) {
        dispatch(savePhotoSuccess(response.data.data.photos))
    }
};

export const saveProfileData = (profile: ProfileType) => async (dispatch: any,getState: any) => {
    let response = await profileAPI.saveProfileData(profile);
    if (response.data.resultCode === 0) {

        const userId=getState().auth.id;
        const data = await profileAPI.getUserProfile(userId);
        dispatch(setUserProfile(data));
    }
    else {
        let errorMessage = response.data.messages.length > 0 ? response.data.messages[0] : 'Oops';
        console.log(errorMessage)
        dispatch(stopSubmit('profileData', {_error: errorMessage}));
        return Promise.reject(errorMessage)
    }
};

export default profileReducer;