import {connect} from 'react-redux'
import React from 'react'
import {
    setInProgress, onPageChanged, follow, unfollow, requestUsers, setCurrentPage, setUsers
} from '../../redux/usersReducer';
import Preloader from '../../common/Preloader/Preloader';
import {compose} from "redux";
import {
    getCurrentPage, getFollowingInProgress,
    getIsLoading, getPageSize,
    getTotalUsersCount,
    getUsers
} from "../../redux/usersSelectors";
import UsersList from "./UsersList";
import {withAuthRedirect} from "../hoc/withAuthRedirect";
import s from './Users.module.css'
import {UsersType} from "../../types/types";
import { AppStateType } from '../../redux/reduxStore';

type MapStatePropsType = {
    currentPage: number
    pageSize: number
    users: Array<UsersType>
    isLoading: boolean
    followingInProgress: Array<number>
    totalUsersCount: number
}

type MapDispathPropsType = {

    requestUsers: (currentPage: number , pageSize: number) => void
    setCurrentPage: (pageNumber: number) => void
    setUsers: (users: Array<UsersType>) => void
    onPageChanged: (currentPageNumber: number, pageSize: number) => void
}

type OwnPropsType = {
    title?: string
}

type PropsType = MapStatePropsType & MapDispathPropsType & OwnPropsType;

class UsersContainer extends React.Component<PropsType> {

    componentDidMount() {
        this.props.requestUsers(this.props.currentPage, this.props.pageSize);
    }

    componentWillUnmount() {
        //this.props.setCurrentPage(1);
        //this.props.setUsers([]);
    }

    loadNextPage = (startIndex: number,stopIndex: number) => {

        let itemsNumber = (stopIndex - startIndex)+1;
        let pageNumber = Math.ceil((startIndex+1)/itemsNumber);
        this.props.requestUsers(pageNumber,itemsNumber)
    };

    render() {
        return (<div className={s.list__container}>

                {this.props.users.length>0 ?
                    <UsersList
                        users={this.props.users}
                        hasNextPage={true}
                        isNextPageLoading={this.props.isLoading}
                        loadNextPage={this.loadNextPage}
                        totalUsers={this.props.totalUsersCount}
                    /> : <Preloader/>
                }
            </div>
        )
    }
}

let mapStateToProps = (state: AppStateType): MapStatePropsType => {

    return {
        users: getUsers(state),
        pageSize: getPageSize(state),
        totalUsersCount: getTotalUsersCount(state),
        currentPage: getCurrentPage(state),
        isLoading: getIsLoading(state),
        followingInProgress: getFollowingInProgress(state)
    }
};

export default compose(
    connect<MapStatePropsType, MapDispathPropsType, OwnPropsType, AppStateType>(mapStateToProps, {
        requestUsers,
        setCurrentPage,
        setUsers,
        onPageChanged
    }),
    withAuthRedirect
)(UsersContainer)
